.service {
  &__content {
    max-width: 1250px;
    padding: 155px 0 0 41px;
    margin: 0 auto;
  }

  &__title {
    position: relative;

    font-family: "Raleway", "Arial", sans-serif;
    font-weight: 900;
    font-size: 30px;
    line-height: 50px;
    letter-spacing: 1px;

    margin: 25px 0 0 3px;

    color: #000000;
    text-transform: uppercase;

    &::after {
      content: "";
      position: absolute;
      top: 24px;
      right: 40px;

      width: 72%;
      height: 2px;

      border-top: 2px solid rgb(0, 0, 0);
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;

    @include list-reset;

    margin: 18px 0 0 0;

    & a {
      color: rgba(48, 48, 48, 0.769);
    }
  }

  &__item {
    width: 29.5%;
    min-height: 499px;
    margin: 0 45px 44px 0;
    padding: 35px 41px 0;

    background-color: rgb(255, 255, 255);
    box-shadow: 0 5px 9px 1px rgba(0, 0, 0, 0.05);

    &:nth-child(3n) {
      margin: 0 0 44px 0;
    }
  }
}

.subservice {
  &__list {
    @include list-reset;
    padding: 25px 0 0 2px;
  }

  &__title {
    display: block;
    font-family: "Roboto", "Arial", sans-serif;
    font-weight: 900;
    font-size: 16px;
    line-height: 1.2;
    text-transform: uppercase;

    color: #000000;
  }

  &__item {
    margin: 0 0 15px 0;
  }
}


@media (max-width: 1023px) {
  .service {
    &__content {
      width: 100%;
      // max-width: 768px;
      padding: 17px 22px 0 22px;
      margin: 0 auto;
    }

    &__title {
      font-family: "Raleway", "Arial", sans-serif;
      font-weight: 900;
      font-size: 26px;
      line-height: 1.2;

      margin: 25px 0 0 0;

      &::after {
        display: none;
      }
    }

    &__list {
      flex-direction: column;
      // max-width: 724px;
      max-height: 340px;
      margin: 22px 0 0 0;
    }

    &__item {
      // width: 352px;
      width: 49%;
      min-height: 50px;
      height: auto;
      margin: 0 20px 10px 0;
      padding: 0;

      box-shadow: none;

      &:nth-child(3n) {
        margin: 0 auto 10px 0;
      }

      &:nth-child(2n) {
        margin: 0 0 10px 0;
      }
    }
  }

  .subservice {
    &__title {
      font-weight: normal;
      font-size: 14px;
      line-height: 1.4;

      // width: 350px;
      width: 100%;
      height: 50px;
      padding: 15px 4px 0 20px;
      margin: 0;

      text-align: left;
      color: rgb(49, 49, 49);
      background-color: rgb(244, 244, 244);
      cursor: pointer;

      &:hover {
        opacity: 0.7;
      }

      &--long {
        padding: 10px 8px 0 20px;
      }
    }

    & input:checked + label {
      background-color: rgb(215, 215, 215);
      color: #000000;
    }

    & input:checked ~ ul {
      display: block;
    }

    &__list {
      display: none;
      padding: 0;
    }

    &__item {
      font-size: 14px;
      line-height: 1.35;

      // width: 350px;
      width: 100%;
      height: 48px;
      margin: 0;

      background-color: rgb(244, 244, 244);
      border-bottom: 1px solid rgb(232, 232, 232);
      overflow: hidden;

      &:hover {
        border: 1px solid rgb(0, 255, 215);
        box-shadow: 0 2px 6.3px 0.7px rgba(0, 255, 216, 0.65);
      }

      & span {
        display: none;
      }
    }

    &__link {
      display: block;
      height: 100%;
      padding: 16px 4px 0 21px;

      &--long {
        padding: 4px 8px 0 21px;
      }
    }
  }
}

@media (max-width: 767px) {
  .service {
    &__content {
      max-width: 100%;
      padding: 19px 0 0 0;
    }

    &__title {
      font-size: 20px;

      margin: 25px 0 0 20px;
    }

    &__list {
      max-height: 100%;
      margin: 22px 0 0 0;
    }

    &__item {
      width: 100%;
    }
  }

  .subservice {
    &__title {
      font-size: 14px;
      line-height: 1.2;

      width: 100%;
      height: 50px;
      padding: 19px 0 0 22px;
      letter-spacing: 1px;

      &--long {
        padding: 5px 0 0 20px;
      }
    }

    &__item {
      font-size: 12px;
      line-height: 1.35;

      width: 100%;
      height: 42px;

      background-color: #ffffff;
    }

    &__link {
      padding: 15px 4px 0 21px;

      &--long {
        padding: 0 4px 0 21px;
      }
    }
  }
}
